.pricing-section {
  background: linear-gradient(to right, #f9f9f9 0%, #aab4f0 100%);
  border-radius: 20px;
  padding: 130px 400px;
  text-align: center;
  max-width: 99%;
  color: #0a0a3a;
 margin-top: 40px;
}

.pricing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pricing-tag {
  font-size: 0.9rem;
  letter-spacing: 2px;
  color: #0a0a3a;
  font-weight: 600;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.pricing-title {
  font-family: 'poppins', 'sans-serif';
  font-size: 4.5rem;
  font-weight: 500;
  color: #0a0a3a;
  margin: 0;
}

.pricing-subtitle {
  font-size: 3rem;
  font-style: italic;
 font-family:'Plus Jakarta Sans';
  color: #0a0a3a;
  margin-top: 10px;
  font-weight: 400;
}

@media (max-width: 768px) {
  .pricing-title {
    font-size: 2.2rem;
  }

  .pricing-subtitle {
    font-size: 1.8rem;
  }

  .pricing-section {
    padding: 70px 15px;
  }
}


/* AppointmentSection Section Start (form) */

.appointment-section {
  background: #fff;
  padding: 100px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #0a0a3a;
}

.appointment-container {
  text-align: center;
  max-width: 800px;
  width: 100%;
}

.appointment-tag {
  display: inline-block;
  background: #fff;
  border: 1px solid #0a0a3a;
  border-radius: 30px;
  padding: 6px 18px;
  font-size: 0.9rem;
  font-weight: 600;
  color: #0a0a3a;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.appointment-title {
    font-size: 3rem;
    font-weight: 500;
    color: #24246a;
    line-height: 1.3;
    margin-bottom: 40px;
}

.appointment-form {
  background: #fff;
  border: 1px solid #eaeaea;
  border-radius: 15px;
  padding: 50px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.form-group label {
  font-size: 0.95rem;
  color: #2e2e50;
  margin-bottom: 8px;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  border: none;
  border-bottom: 1px solid #ccc;
  padding: 10px;
  font-size: 1rem;
  outline: none;
  background: transparent;
}

.form-group textarea {
  min-height: 80px;
  resize: none;
}

.appointment-btn {
  width: 100%;
  background:  #13195c;
  color: #fff;
  border: none;
  border-radius: 40px;
  padding: 16px 20px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  margin-top: 30px;
  transition: 0.3s ease;
}

.appointment-btn:hover {
  background: #1b1b6b;
}


/* Transformsection Section Css Start */

.transform-section {
  background-color: #f9fafb;
  padding: 80px 0;
  display: flex;
  justify-content: center;
}

.transform-container {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1150px;
  gap: 60px;
}

.transform-image img {
    width: 500px;
    height: auto;
    border-radius: 20px;
    background: linear-gradient(90deg, #c8c7ff, #d8e3ff);
}

.transform-content {
  flex: 1;
}

.transform-content h2 {
    font-size: 3.2rem;
    color: #0d1454;
    margin-bottom: 20px;
    font-weight: 500;
}

.transform-content p {
  color: #2c2f55;
  font-size: 1.2rem;
  line-height: 1.6;
}

.contact-info {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 25px;
}

.phone-icon {
  width: 50px;
  height: 50px;
  background: #f2f3f6;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0d1454;
  font-size: 20px;
}

.call-label {
  color: #777;
  font-size: 0.9rem;
  margin-bottom: 5px;
}

.phone-number {
  color: #0d1454;
  font-weight: 600;
  font-size: 1.1rem;
}

.schedule-btn {
  background-color: #0d1454;
  color: #fff;
  border: none;
  padding: 14px 28px;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
}

.schedule-btn span {
  background: #fff;
  color: #0d1454;
  border-radius: 50%;
  padding: 5px 8px;
  font-size: 1.1rem;
}

.schedule-btn:hover {
  transform: scale(1.05);
}

